.badge-category {
  padding: 2px 10px;
  border-radius: 25px;
  color: #fff;
}

.horror {
  background:darkred !important;
}
.fable {
  background: #f8ce0b !important;
}
.tutorial {
  background: darkorange !important;
}
.crime {
  background: darkslateblue !important;
}
.romance{
  background:red !important;
}
.cover img {
  border-radius: 10px;
}
.cover {
  color: #666;
}

.cover h5 {
  font-style: italic;
  font-weight: lighter;
}
.cover p {
  font-size: 13px;
}

.rating-all {
  font-size: 20px;
  display: inline-block;

  position: relative;
  font-family: FontAwesome;
}

.rating-all::before {
  content: "\f006 \f006 \f006 \f006 \f006";
}

.rating-inner {
  position: absolute;
  top: 0;
  left: 0;
  white-space: nowrap;
  overflow: hidden;
  width: 0;
}

.rating-inner::before {
  content: "\f005 \f005 \f005 \f005 \f005";
  color: #f8ce0b;
}

.animateTest {
  transition: opacity 0.3s linear;
}

/*  NAV FILTER */

.nav-item {
  cursor: pointer;
}
.nav-active {
  background: #f51167;
  color: #fff;
  transition: background 0.5s ease;
  border-radius: 25px;
  padding: 0 20px;
}
